import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'
import applyForTest from '@/view/crowdTest/apply-for-test.vue'; //申请众测
import verifyAddress from '@/view/crowdTest/verify-address.vue' //确认申请地址


Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: '/apply-for-test.html',
  routes: [{
    path: '/',
    name: 'applyForTest',
    component: applyForTest
  }, {
    path: '/:userId',
    name: 'verifyAddress',
    component: verifyAddress
  }]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



